.course-center {
  padding-top: 60px;
  width: 100%;
  background: #F6F7FB;
  padding-bottom: 60px;
  min-width: 1366px;
}
.course-center .center-title {
  margin: 40px 0 16px 26px;
}
.course-center .center-title span {
  font-size: 28px;
  line-height: 120.7%;
  color: #2A283D;
}
.course-center .center-title .ant-tabs {
  display: inline-block;
  vertical-align: -35px;
  margin-left: 40px;
}
.course-center .center-title .ant-tabs .ant-tabs-tab {
  color: #2A283D;
  opacity: 0.4;
  font-size: 14px;
  margin: 0 20px 0 0;
}
.course-center .center-title .ant-tabs .ant-tabs-tab-active {
  color: #2A283D;
  opacity: 1;
}
.course-center .center-content .center-content-left {
  padding: 0 28px;
}
.course-center .center-content .center-content-left .ant-card-body {
  padding: 10px 0;
}
.course-center .center-content .center-content-left .ant-card-body .ant-select {
  padding: 0 10px;
}
.course-center .center-content .center-content-left .select-bagr .ant-select-selection {
  background: #F5F5F5;
  border: 1px solid #E6E6E6;
  box-sizing: border-box;
  border-radius: 3px;
}
.course-center .center-content .center-content-left .select-bagr .ant-tree-switcher-noop i {
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background: #3BB9FF;
  display: inline-block;
  margin-bottom: 4px;
}
.course-center .center-content .center-content-left .select-bagr .ant-tree-switcher-noop i svg {
  display: none;
}
