.course-center .center-content .center-content-left .select-bagr .ant-tree .ant-tree-node-content-wrapper::selection,
.course-center .center-content .center-content-left .select-bagr .ant-tree .ant-tree-node-content-wrapper.ant-tree-treenode-switcher-open::selection {
  background: #1890ff !important;
  color: #fff !important;
  box-shadow: none!important;
}
.course-center .center-content .center-content-left .select-bagr .ant-tree-treenode-switcher-open .ant-tree-node-content-wrapper.ant-tree-node-content-wrapper-open {
  background: #1890ff !important;
  color: #fff !important;
  box-shadow: none!important;
}
.course-center .center-content .center-content-left .select-bagr .ant-tree-treenode-switcher-open .ant-tree-node-content-wrapper.ant-tree-node-content-wrapper-open .ant-tree-node-content-wrapper.ant-tree-node-content-wrapper-normal {
  background: #1890ff !important;
  color: #fff !important;
  box-shadow: none!important;
}
.course-center .center-content .center-content-left .select-bagr .ant-tree-treenode-switcher-open .ant-tree-treenode-switcher-open .ant-tree-child-tree.ant-tree-child-tree-open .ant-tree-treenode-switcher-close .ant-tree-node-content-wrapper::selection,
.course-center .center-content .center-content-left .select-bagr .ant-tree-treenode-switcher-open .ant-tree-treenode-switcher-open .ant-tree-child-tree.ant-tree-child-tree-open .ant-tree-treenode-switcher-close .ant-tree-node-content-wrapper::-moz-selection,
.course-center .center-content .center-content-left .select-bagr .ant-tree-treenode-switcher-open .ant-tree-treenode-switcher-open .ant-tree-child-tree.ant-tree-child-tree-open .ant-tree-treenode-switcher-close .ant-tree-node-content-wrapper:focus {
  background: #1890ff !important;
  color: #fff !important;
  box-shadow: none!important;
}
.course-center .center-content .center-content-left .select-bagr .ant-tree .ant-tree-node-content-wrapper.ant-tree-node-selected,
.course-center .center-content .center-content-left .select-bagr .ant-tree .ant-tree-node-content-wrapper.ant-tree-node-selected {
  background: #1890ff !important;
  color: #fff !important;
  box-shadow: none!important;
}
.course-center .center-content .ant-card {
  border-radius: 4.5705px;
}
.course-center .center-content .no-content {
  min-height: 400px;
}
.course-center .center-content .no-content .ant-empty {
  position: absolute;
  left: 40%;
  top: 30%;
  transform: translate(-30%, -50%);
}
.course-center .center-content .center-content-search {
  margin-bottom: 30px;
}
.course-center .center-content .center-content-search .center-content-paper .ant-card-body {
  padding: 30px 24px 0 0;
}
.course-center .center-content .center-content-search {
  margin-bottom: 30px;
}
.course-center .center-content .center-content-search .center-content-paper .ant-card-body {
  padding: 30px 24px 0 0;
}
.course-center .center-content .center-content-search .center-content-paper .ant-row {
  margin-bottom: 30px;
}
