.course-center .center-content .center-content-center .paper-common {
  margin-bottom: 20px;
}
.course-center .center-content .center-content-center .paper-common .paper-year {
  color: #2A283D;
  font-weight: bold;
  display: inline-block;
  text-align: center;
  height: 30px;
  line-height: 30px;
}
.course-center .center-content .center-content-center .paper-common .ant-radio-button-wrapper {
  color: #000;
  background: none;
  border: none;
}
.course-center .center-content .center-content-center .paper-common .ant-radio-button-wrapper::before {
  width: 0;
}
.course-center .center-content .center-content-center .paper-common .ant-radio-button-wrapper.ant-radio-button-wrapper-checked {
  background: #3BB9FF;
  border-radius: 2px;
  height: 26px;
  line-height: 26px;
  color: #fff;
}
.course-center .center-content .center-content-center .paper-common .actives {
  color: #2cbde6;
  border-color: #2cbde6;
}
.course-center .center-content .center-content-center .paper-common .ant-divider-horizontal {
  margin: 16px 0;
}
.course-center .center-content .center-content-center .paper-common .paper-common-search {
  text-align: right;
}
.course-center .center-content .center-content-center .paper-common .paper-common-search .ant-input-affix-wrapper .ant-input {
  background: #F4F4F4;
  border-radius: 13.5px;
  height: 32px;
  opacity: 0.7;
}
.course-center .center-content .center-content-center .paper-common .paper-common-search .paper-common-totale {
  margin-left: 20px;
  color: #000000;
  opacity: 0.3;
}
.course-center .center-content .center-content-center .paper-common-students .paper-common-btn .ant-btn {
  border: none;
  background: none;
  margin: 0 10px 5px 0;
  background: #EFEFEF;
  border-radius: 2px;
}
.course-center .center-content .center-content-center .paper-common-students .paper-common-btn .ant-btn.ant-btn-primary {
  width: 110px;
  height: 32px;
  background: #3BB9FF !important;
  border-radius: 2px;
}
.course-center .center-content .center-content-center .paper-common-students .paper-common-btn .ant-btn.ant-btn-primary span {
  color: #fff !important;
}
.course-center .center-content .center-content-center .paper-common-students .paper-common-btn .ant-btn span {
  font-size: 14px;
  color: #000;
  font-weight: 400;
}
.course-center .center-content .center-content-center .paper-center-div {
  width: 16%;
  box-sizing: border-box;
  border-radius: 3px;
  margin: 0 5% 3% 0;
  cursor: pointer;
}
.course-center .center-content .center-content-center .paper-center-div .pic-img {
  min-width: 175px;
  min-height: 105px;
  height: 167px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  background: url(../../../assets/images/placeholder_bg.png) no-repeat;
  background-size: 100% 100%;
}
.course-center .center-content .center-content-center .paper-center-div .pic-img img {
  display: block;
  width: 100%;
  height: 100%;
}
.course-center .center-content .center-content-center .paper-center-div .pic-img > span {
  padding: 2px 6px;
  position: absolute;
  left: 10px;
  bottom: 10px;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 2px;
  color: #fff;
  font-size: 12px;
}
.course-center .center-content .center-content-center .paper-center-div .pic-img > span .course-logo-l {
  width: 12px !important;
  height: 12px !important;
  vertical-align: -1px;
  margin-right: 5px;
  display: inline-block;
}
.course-center .center-content .center-content-center .paper-center-div h4 {
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 120.7%;
  color: #2A283D;
  margin: 8px 0 6px 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  height: 21px;
}
.course-center .center-content .center-content-center .paper-center-div p {
  margin: 0 0 4px 0!important;
  width: 100%;
  display: block;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 20px;
  color: #2A283D;
  opacity: 0.8;
  height: 20px;
}
.course-center .center-content .center-content-center .paper-center-div p .i {
  color: #2A283D;
  opacity: 0.8;
  font-size: 12px;
  margin: 0 10px;
}
.course-center .center-content .center-content-center .paper-center-div p span {
  font-size: 12px;
  color: #000000;
  opacity: 0.3;
  width: 30%;
  float: left;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.course-center .center-content .center-content-center .paper-center-div p span img {
  height: 10px;
  display: inline-block;
  margin-right: 10px;
  width: auto;
  min-width: auto;
  min-height: auto;
  vertical-align: initial;
}
.course-center .center-content .center-content-center .paper-center-div p .span-b {
  width: 70%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.course-center .center-content .center-content-center .paper-center-div p .span-a {
  width: 70%;
  overflow: hidden;
}
.course-center .center-content .center-content-center .paper-center-div p .span {
  padding-left: 10px;
}
.course-center .center-content .center-content-center .paper-center-div p:first-child span {
  font-size: 14px;
  color: #000000;
  margin-left: 10px;
  opacity: 01;
}
.course-center .center-content .center-content-center .paper-center-div:nth-child(5n) {
  margin-right: 0;
}
.course-center .center-content .center-content-center .paper-center-div:hover h4 {
  color: #3BB9FF;
}
.course-center .center-content .my-collect-tree {
  display: block;
  overflow-y: auto;
  padding: 0 10px;
}
.course-center .center-content .my-collect-tree .anticon svg {
  border: 1px solid #3BB9FF;
  color: #DEF3FF;
}
.course-center .center-content .my-collect-tree .anticon svg path:first-child {
  color: #3BB9FF;
}
.course-center .center-content .my-collect-tree .TreeCircleIcon {
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background: #3BB9FF;
  display: inline-block;
  margin-bottom: 4px;
}
.course-center .center-content .my-collect-tree::-webkit-scrollbar {
  /*滚动条整体样式*/
  width: 6px;
  height: 6px;
  background-color: #ffffff;
}
.course-center .center-content .my-collect-tree::-webkit-scrollbar-thumb {
  /*滚动条里面小方块*/
  -webkit-box-shadow: inset 0 0 6px #DDDDDD;
  background-color: #DDDDDD;
}
.course-center .center-content .my-collect-tree::-webkit-scrollbar-track {
  /*滚动条里面轨道*/
  -webkit-box-shadow: inset 0 0 6px #ffff;
  border-radius: 40px;
}
html {
  overflow-y: auto;
  overflow-x: auto;
}
